nav#main-nav {
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(0, 0);
  width: 100%;
  height: 50px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.6);
  background: white;
  color: #0f2f57;
  padding: 0 2rem;
  z-index: 15; }
  nav#main-nav h3 {
    font-size: 1.5rem; }
    nav#main-nav h3 span {
      max-width: 0;
      transition: 0.6s ease-in;
      display: inline-block;
      overflow: hidden;
      vertical-align: bottom;
      opacity: 0;
      margin-right: 5px; }
    nav#main-nav h3:hover span {
      max-width: 250px;
      opacity: 1; }
  nav#main-nav .hamburger {
    display: none; }
  @media (max-width: 899px) {
    nav#main-nav .hamburger {
      display: block;
      width: 48px;
      height: 48px;
      cursor: pointer; }
      nav#main-nav .hamburger svg #hamburger .dash {
        transition: all .6s ease; }
        nav#main-nav .hamburger svg #hamburger .dash.center {
          opacity: 1; }
        nav#main-nav .hamburger svg #hamburger .dash.top, nav#main-nav .hamburger svg #hamburger .dash.bottom {
          transform: rotate(0deg) translate(0%, 0%); }
      nav#main-nav .hamburger.toggled svg #hamburger .dash {
        transition: all .6s ease; }
        nav#main-nav .hamburger.toggled svg #hamburger .dash.top {
          transform: rotate(45deg) translate(25%, -25%); }
        nav#main-nav .hamburger.toggled svg #hamburger .dash.center {
          opacity: 0; }
        nav#main-nav .hamburger.toggled svg #hamburger .dash.bottom {
          transform: rotate(-45deg) translate(-50%, 3%); }
    nav#main-nav ul.links {
      width: 100%;
      height: calc(100vh - 50px);
      position: fixed;
      left: -150vw;
      top: 50px;
      text-align: center;
      flex-direction: column;
      background: #0087bd;
      transition: .6s ease; }
      nav#main-nav ul.links li {
        display: block;
        height: 100px;
        margin: 2vh 0;
        font-size: 2rem;
        font-weight: bold;
        text-transform: uppercase; }
        nav#main-nav ul.links li a {
          color: #f6f5f5; }
          nav#main-nav ul.links li a::before {
            background-color: #f6f5f5; }
      nav#main-nav ul.links.expanded {
        left: 0; } }
