/* FONTS IMPORT */
@font-face {
  font-family: "open24";
  src: url("../fonts/Open-24.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins/Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins/Regular-Italic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic; }

