  src: url("../fonts/poppins/Black-Italic.ttf") format("truetype");
  font-weight: bold;
  font-style: italic; }

/* GLOBAL FONT STYLES */
p, .is-bold, strong, .is-italic, em, .content, h1, h2, h3, h4, h5, a {
  font-family: poppins, sans-serif; }

p.legend {
  font-style: italic;
  color: #7b7b7b; }

p.is-bold, p strong {
  font-weight: bold; }

p.is-italic, p em {
  font-style: italic; }

p.is-centered, p em {
  text-align: center; }

a, h1, h2, h3, h4, h5 {
  color: #0f2f57;
  transition: color .3s; }
  a:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover {
    color: #0087bd; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

h1, h2, h3, h4, h5 {
  letter-spacing: 1.5px; }

/* Custom Links styles & hover */
a:not(.quarter-link) {
  cursor: pointer;
  position: relative;
  padding: .25rem 0; }
  a:not(.quarter-link)::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -100%);
    width: 0;
    height: 2px;
    background: #0087bd;
    transition: 0.3s ease-in; }
  a:not(.quarter-link):hover::before, a:not(.quarter-link).selected::before {
    width: 80%; }
  a:not(.quarter-link).selected {
    color: #0087bd; }

/* Custom Typist element */
.Typist {
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  text-align: center;
  font-family: open24 !important;
  font-size: 2.5rem; }
  @media (max-width: 628px) {
    .Typist {
      font-size: 1.5rem;
      margin: 0; } }
  .Typist .Cursor {
    padding-left: 1rem;
    vertical-align: middle;
    color: currentColor;
    font-family: Helvetica, sans-serif;
    animation: blinker .7s linear infinite; }

