/* GLOBAL SCROLLBARS STYLES */
::-webkit-scrollbar {
  width: 5px;
  z-index: 0; }

/* scrollbar width */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(246, 245, 245, 0.6); }

/* Track */
::-webkit-scrollbar-thumb {
  background: #ee6f57;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb:hover {
  background: #0087bd; }

