/* RESET DEFAULT SYLES GLOBALLY*/
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

html, body, #root, #app {
  min-height: 100vh; }

#app {
  padding-top: 50px; }

.icon-svg svg {
  fill: currentColor;
  width: 100%; }

ul.links {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }
  ul.links li {
    margin: 0 1rem; }

li {
  list-style-type: none; }

a {
  text-decoration: none; }

.animated {
  opacity: 0; }

img, .icon-svg {
  width: 100%; }
  img > div, .icon-svg > div {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center; }

/* APPLICATION'S BACKGROUND */
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(ellipse farthest-corner at 90% 90%, #0087bd, #0f2f57 100%);
  background-size: 300% 300%;
  animation: gradient 20s ease infinite; }

/* UI MAIN ROUTES SECTIONS */
section.main-container {
  width: 100%;
  height: calc(100vh - 50px); }
  section.main-container section.tab.cols, section.main-container .content.cols {
    flex-flow: column nowrap; }
    section.main-container section.tab.cols.start, section.main-container .content.cols.start {
      justify-content: start; }
  section.main-container section.tab.wrap, section.main-container .content.wrap {
    flex-wrap: wrap;
    align-content: center; }
  section.main-container section.tab {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: calc(100vh - 50px);
    overflow: hidden;
    overflow-y: auto;
    padding-top: 40px; }
    section.main-container section.tab .Typist {
      margin: 1rem 0; }
    section.main-container section.tab ul.list li {
      margin: .5rem 1.5rem;
      line-height: 1.8rem;
      font-weight: bold;
      list-style-type: disclosure-closed;
      width: 90%; }
  section.main-container .content {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: calc(100vh - 130px); }

.construction {
  width: 40vw;
  height: auto;
  padding: 1rem 2rem;
  min-width: 300px;
  margin: 2rem auto;
  background: #f6f5f5;
  box-shadow: 1px 2px 4px rgba(56, 56, 56, 0.6); }
  .construction h1 {
    font-size: 2rem;
    text-align: center;
    text-transform: uppercase;
    color: #0f2f57;
    border-bottom: 2px solid currentColor; }
  .construction p {
    padding: 0.5rem 1rem;
    color: #7b7b7b; }

#algos, #apps {
  width: 100vw;
  height: calc(100vh - 130px); }

.icon-svg.close, .nav {
  filter: drop-shadow(1px 2px 4px rgba(0, 0, 0, 0.3));
  cursor: pointer; }

@keyframes timelineOpening {
  0% {
    opacity: 1;
    height: 0;
    width: 100px;
    margin: 0 auto; }
  50% {
    height: calc(100vh - 130px);
    width: 100px; }
  100% {
    width: 100%;
    margin: 0 auto;
    opacity: 1; } }

@keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes blinker {
  0% {
    opacity: 1.0; }
  50% {
    opacity: 0.0; }
  100% {
    opacity: 1.0; } }

@keyframes slideLeft {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0); } }

@keyframes slideDown {
  from {
    transform: translateY(-100%); }
  to {
    transform: translateY(0); } }

@keyframes slideUp {
  from {
    transform: translateY(100%); }
  to {
    transform: translateY(0); } }

@keyframes slideRight {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

@keyframes zoomInDown {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
    transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }
  60% {
    opacity: 1;
    -webkit-transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
    transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
    -webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1); }
  100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
    transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
    -webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1); } }

@keyframes flipInX {
  0% {
    -webkit-transform: perspective(400px) rotateX(90deg);
    transform: perspective(400px) rotateX(90deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 0; }
  40% {
    -webkit-transform: perspective(400px) rotateX(-20deg);
    transform: perspective(400px) rotateX(-20deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in; }
  60% {
    -webkit-transform: perspective(400px) rotateX(10deg);
    transform: perspective(400px) rotateX(10deg);
    opacity: 1; }
  80% {
    -webkit-transform: perspective(400px) rotateX(-5deg);
    transform: perspective(400px) rotateX(-5deg); }
  100% {
    -webkit-transform: perspective(400px);
    transform: perspective(400px);
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0); }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0); } }

@keyframes bounceInRight {
  0%, 60%, 75%, 90%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    -webkit-transform: translate3d(3000px, 0, 0) scaleX(3);
    transform: translate3d(3000px, 0, 0) scaleX(3); }
  60% {
    opacity: 1;
    -webkit-transform: translate3d(-25px, 0, 0) scaleX(1);
    transform: translate3d(-25px, 0, 0) scaleX(1); }
  75% {
    -webkit-transform: translate3d(10px, 0, 0) scaleX(0.98);
    transform: translate3d(10px, 0, 0) scaleX(0.98); }
  90% {
    -webkit-transform: translate3d(-5px, 0, 0) scaleX(0.995);
    transform: translate3d(-5px, 0, 0) scaleX(0.995); }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1; } }
