#videos-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: flex-start;
  padding: 0 1rem 0;
  width: 100%;
  min-height: calc(100vh - 130px); }
  #videos-container .video-details, #videos-container .next {
    padding: 1rem;
    padding-top: .5rem; }
  #videos-container .video-details {
    width: calc(100vw - 25px); }
    #videos-container .video-details .screen {
      width: 100%;
      display: flex; }
      #videos-container .video-details .screen iframe {
        border: none;
        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4);
        width: 100%;
        height: 40vw;
        cursor: zoom-in;
        margin: 0 auto; }
    #videos-container .video-details .infos {
      padding-top: 1rem;
      width: 100%; }
      #videos-container .video-details .infos h3 {
        font-size: 1.5rem;
        border-bottom: 1px solid currentColor;
        margin-bottom: 1rem; }
      #videos-container .video-details .infos p {
        padding: 0 1rem 0 1rem;
        max-width: 95%; }
        #videos-container .video-details .infos p span {
          display: block; }
  #videos-container .next {
    width: 425px; }
    #videos-container .next .collection-preview {
      cursor: pointer;
      padding: .5rem;
      max-width: 375px;
      margin-bottom: 1rem;
      border-radius: 4px; }
      #videos-container .next .collection-preview h3 {
        font-size: 1.25rem;
        color: #383838;
        text-transform: uppercase;
        border-bottom: 1px solid currentColor;
        margin-bottom: .5rem; }
      #videos-container .next .collection-preview .covers {
        width: 100%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-start;
        align-items: center; }
        #videos-container .next .collection-preview .covers .cover {
          width: 325px;
          height: 105px;
          display: flex;
          flex-flow: row nowrap;
          justify-content: flex-start;
          align-items: flex-start;
          overflow: hidden;
          margin: 0.5rem 1rem;
          transition: 0.3s ease-in; }
          #videos-container .next .collection-preview .covers .cover img {
            width: 155px;
            height: 105px;
            min-width: 155px;
            min-height: 105px;
            object-fit: cover;
            transition: 0.6s ease-in;
            filter: blur(0) brightness(1);
            box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4); }
          #videos-container .next .collection-preview .covers .cover .preview-infos {
            width: 100%;
            height: 100%;
            padding-left: .85rem; }
            #videos-container .next .collection-preview .covers .cover .preview-infos p {
              width: 100%;
              color: #0f2f57;
              font-weight: bold;
              transition: 0.3s ease-in; }
            #videos-container .next .collection-preview .covers .cover .preview-infos em {
              font-size: .8rem;
              color: #7b7b7b; }
          #videos-container .next .collection-preview .covers .cover:hover {
            box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4); }
  @media (max-width: 899px) {
    #videos-container {
      flex-direction: column;
      padding: 0; }
      #videos-container .video-details, #videos-container .next {
        width: 100vw;
        height: auto;
        padding: 0; }
      #videos-container .screen {
        width: 100vw; }
        #videos-container .screen iframe {
          width: 100vw !important;
          height: 80vw !important; }
      #videos-container .infos {
        padding: .5rem; }
        #videos-container .infos h3 {
          text-align: center; }
      #videos-container .next {
        padding: .5rem;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-evenly;
        align-items: flex-start; } }
