#works {
  position: relative;
  width: 100%;
  height: calc(100vh - 50px);
  overflow: hidden; }
  #works .quarter {
    position: absolute;
    width: 50%;
    height: 50%;
    background: rgba(0, 0, 0, 0.2);
    transition: 0.3s ease-in;
    z-index: 2;
    overflow: hidden; }
    #works .quarter:nth-of-type(1) {
      top: 0;
      left: 0;
      border-right: 1px solid white;
      border-bottom: 1px solid white;
      animation: slideLeft 1s ease-in; }
    #works .quarter:nth-of-type(2) {
      top: 0;
      left: 50%;
      border-left: 1px solid white;
      border-bottom: 1px solid white;
      animation: slideDown 1s ease-in; }
      #works .quarter:nth-of-type(2).full {
        left: 0; }
    #works .quarter:nth-of-type(3) {
      top: 50%;
      left: 0;
      border-right: 1px solid white;
      border-top: 1px solid white;
      animation: slideUp 1s ease-in; }
      #works .quarter:nth-of-type(3).full {
        top: 0; }
    #works .quarter:nth-of-type(4) {
      top: 50%;
      left: 50%;
      border-left: 1px solid white;
      border-top: 1px solid white;
      animation: slideRight 1s ease-in; }
      #works .quarter:nth-of-type(4).full {
        top: 0;
        left: 0; }
    #works .quarter .quarter-link > header {
      width: 100%;
      height: 100%;
      position: relative;
      cursor: pointer; }
      #works .quarter .quarter-link > header .Typist {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        transition: 0.6s ease-in; }
      #works .quarter .quarter-link > header .category-icon {
        position: absolute;
        width: 68px;
        height: 68px;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        transition: 0.3s ease-in;
        color: white; }
    #works .quarter:hover:not(.full) {
      background: rgba(0, 0, 0, 0.4); }
      #works .quarter:hover:not(.full) header .Typist {
        top: 60%; }
      #works .quarter:hover:not(.full) header .category-icon {
        opacity: 1; }
    #works .quarter section.quarter-container {
      display: none;
      opacity: 0;
      animation: fadeIn 1s .3s forwards; }
    #works .quarter .close {
      position: absolute;
