      right: 10px;
      cursor: pointer;
      z-index: 5;
      color: #0f2f57;
      display: none; }
      #works .quarter .close.white {
        color: white; }
      #works .quarter .close:hover::before {
        width: 0; }
    #works .quarter.full {
      width: 100%;
      height: 100%;
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: center;
      background: whitesmoke;
      z-index: 10; }
      #works .quarter.full > a {
        width: 100%; }
      #works .quarter.full .quarter-link > header {
        display: none; }
        #works .quarter.full .quarter-link > header .Typist {
          top: 30px;
          text-align: right;
