          padding-right: 60px;
          text-shadow: none !important; }
          #works .quarter.full .quarter-link > header .Typist, #works .quarter.full .quarter-link > header .Typist .span.Cursor {
            color: #0f2f57 !important; }
        #works .quarter.full .quarter-link > header .category-icon {
